import React, { Component } from 'react';
import {
    CCarousel,
    CCarouselControl,
    CCarouselIndicators,
    CCarouselInner,
    CCarouselItem,
    CCol,
    CContainer,
    CRow 
  } from '@coreui/react'
  import { NavLink } from 'react-router-dom'
  import ban1 from "../../assets/img/ban1.jpg";
  import ban2 from "../../assets/img/ban2.jpg";
  import ban3 from "../../assets/img/ban3.jpg";
  import ban4 from "../../assets/img/ban4.jpg";
  import ban5 from "../../assets/img/ban5.jpg";
  import ban6 from "../../assets/img/ban6.jpg";
  import Card from '../vendorComponent/Card';
  import "../../scss/Card.scss";
  import { faIdCardAlt, faWallet, faScroll, faUniversity, faPaste, faSignOutAlt} from '@fortawesome/free-solid-svg-icons';

class Dashboard extends Component {
    constructor(){
        super()
        this.state={
          all:true,
          cate:false,
          category:[],
          isCity:"",
          cityname:[],
          cityError:"",
          subcate:false,
          subcategory:[],
        }
      }
    render() {
        return (
            <div>
         <CContainer>
         
          {/* <CCardBody  className="Ccard_dashv"> */}
            <CCarousel animate autoSlide={3000}style={{marginLeft:"-2rem",marginRight:'-1.9rem',marginTop:'-1rem'}}>
              <CCarouselIndicators/>
              <CCarouselInner>
                <CCarouselItem>
                  <img className="d-block w-100" src={ban1} width="350px" height="150px" alt="slide 1"/>
                </CCarouselItem>
                <CCarouselItem>
                  <img className="d-block w-100" src={ban2} width="350px" height="150px" alt="slide 2"/>
                </CCarouselItem>
                <CCarouselItem>
                  <img className="d-block w-100" src={ban3} width="350px" height="150px" alt="slide 3"/>
                </CCarouselItem>
                <CCarouselItem>
                  <img className="d-block w-100" src={ban4} width="350px" height="150px" alt="slide 4"/>
                </CCarouselItem>
                <CCarouselItem>
                  <img className="d-block w-100" src={ban5} width="350px" height="150px" alt="slide 5"/>
                </CCarouselItem>
                <CCarouselItem>
                  <img className="d-block w-100" src={ban6} width="350px" height="150px" alt="slide 6"/>
                </CCarouselItem>
              </CCarouselInner>
              <CCarouselControl direction="prev"/>
              <CCarouselControl direction="next"/>
            </CCarousel>
          {/* </CCardBody> */}
         
     <center> <h2 style={{marginTop:".5rem"}}>Dashboard</h2> </center>
      <CRow>
         <CCol xs="6" sm="6" md="6" className="card_col">
         <NavLink to="/myprofile"><Card src={faIdCardAlt} heading="My Profile" /></NavLink>
        </CCol>

        <CCol xs="6" sm="6" md="6" className="card_col">
        <NavLink to="/wallet"><Card src={faWallet} heading="Wallet" /></NavLink>
        </CCol>

        <CCol xs="6" sm="6" md="6"  className="card_col" >
        <NavLink to="/Order"><Card src={faScroll} heading="Orders" /></NavLink>
        </CCol>

        <CCol xs="6" sm="6" md="6"  className="card_col">
        <NavLink to="/transactionsum"><Card src={faUniversity} heading="Transaction Summary" /></NavLink>
        </CCol>

        <CCol xs="6" sm="6" md="6" className="card_col">
        <NavLink to="/report"><Card src={faPaste} heading="Reports" /></NavLink>
        </CCol>

        <CCol xs="6" sm="6" md="6" className="card_col" >
        <NavLink to="/signout"><Card src={faSignOutAlt} heading="SignOut" /></NavLink>
        </CCol>

        <CCol xs="12" sm="6" md="12"  className="card_col" >
        <NavLink to="/transactionold"><Card src={faUniversity} heading="Transaction Summary(old)" /></NavLink>
        </CCol>
       
  </CRow>
  </CContainer>
  </div>
        );
    }
}

export default Dashboard;