import React from 'react';
import {
    CCard,
    CCardBody
  } from '@coreui/react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import "../../scss/Card.scss";

function Card(props) {
    return (
        <CCard  className="Ccard_item" >
        <CCardBody>
      
     <center> <FontAwesomeIcon icon={props.src}  className="Font_logo"/></center>

     <br/>
            <p style={{fontSize:'15px'}}><b>{props.heading}</b></p>
          
        </CCardBody>
        
      </CCard>
    );
}

export default Card;